import React from "react";
import { selectAssets } from "../../features/walletConnectSlice";
import { useAppSelector } from "../../store/hooks";
import AccountAssets from "../AccountAssets";
import LoadingIcon from "../LoadingIcon";
import { connectedAddress } from "../../store/connector";
import { ChainType } from "../../helpers/api";

const SiteBody: React.FC = () => {
  var { fetching: loading, address, chain } = useAppSelector((state) => state.walletConnect);
  const assets = useAppSelector(selectAssets);
  var asset_Optin = false;
  var asset_ID = "";

  if (chain === ChainType.MainNet) {
    const Optin = assets.filter((asset) => (asset.id === 812719107));
    console.log(Optin);
    if (Optin.length === 0) {
      asset_Optin = true;
      asset_ID = "812719107";
    }
  }
  else if (chain === ChainType.TestNet) {
    const Optin = assets.filter((asset) => (asset.id === 100203263));
    console.log(Optin);
    if (Optin.length === 0) {
      asset_Optin = true;
      asset_ID = "100203263";
    }
  }
  //console.log("Assets body: " + assets)

  return (
    <div className="site-body">
      <div className="site-body-inner">
          {loading ? <LoadingIcon /> : assets.length < 3 ? <b><i>Connect Wallet to View Account Balances</i></b> : <AccountAssets assets={assets} />}
          { asset_Optin === true ? <div className="asset-button"><button className="t_Button">Opt in ASA #: {asset_ID}</button></div> : "" } 
          {assets.length < 3 ? <div className="site-body-empty"></div> : <h2>Lucky Pools</h2> }
          <label><b><i>Project Lauch Coming Soon....</i></b></label>
      </div>
    </div>
  );
};

export default SiteBody;
